import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzTableQueryParams } from 'ng-zorro-antd/table';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
declare const lightGallery: any;

import {
  Gender,
  GetProductRequestsGQL,
  GetProductRequestsQuery,
  GetProductRequestsQueryVariables,
  OrderBy,
  ProductRequestFieldFragment,
  UpdateProductRequestGQL,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-product-request-list',
  templateUrl: './product-request-list.component.html',
  styleUrls: ['./product-request-list.component.scss'],
})
export class ProductRequestListComponent implements OnInit, OnDestroy {
  Gender = Gender;
  destroy$: Subject<boolean> = new Subject();
  requests: Array<ProductRequestFieldFragment> = [];
  requestsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetProductRequestsQuery, GetProductRequestsQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;
  switchings: Array<boolean> = [];

  constructor(
    private getProductRequestsGQL: GetProductRequestsGQL,
    private route: ActivatedRoute,
    private router: Router,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private updateProductRequestGQL: UpdateProductRequestGQL,
  ) {}

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getProductRequestsGQL.watch(
            this.getVariables(),
            {
              notifyOnNetworkStatusChange: true,
              fetchPolicy: 'network-only',
            },
          );
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(({ data, loading, error, errors }) => {
              this.loading = loading;
              if (loading) {
                return;
              }

              if (error || errors) {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
                return;
              }

              if (data) {
                this.requests = [...data.productRequests];
                this.requestsCount =
                  data.productRequestsConnection.aggregate.count;
                this.switchings = Array(this.requestsCount).fill(false);
              }
            });
        }
      });
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetProductRequestsQueryVariables {
    return {
      where: {},
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: {
        id: OrderBy.Desc,
      },
    };
  }

  handleQueryParamsChange(params: NzTableQueryParams): void {
    this.router.navigate([], {
      queryParams: {
        page: params.pageIndex,
        size: params.pageSize,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  trackById(index: number, item: any): number {
    return item.id;
  }

  viewImages(images: string[], requestId: number): void {
    lightGallery(document.getElementById(`lightgallery_${requestId}`), {
      dynamic: true,
      dynamicEl: images.map((src) => ({
        src,
      })),
    });
  }

  updateStatus(request: ProductRequestFieldFragment, index: number): void {
    this.switchings[index] = true;
    this.updateProductRequestGQL
      .mutate(
        {
          where: {
            id: request.id,
          },
          data: {
            isDone: !request.isDone,
          },
        },
        {
          errorPolicy: 'all',
        },
      )
      .pipe(takeUntil(this.destroy$))
      .subscribe(
        ({ errors, data }) => {
          this.switchings[index] = false;
          if (errors) {
            this.msgService.error(
              this.translateService.instant('product.update_status_error'),
            );
            return;
          }

          if (data) {
            this.msgService.success(
              this.translateService.instant('product.update_status_success'),
            );
            this.requests[index] = {
              ...request,
              isDone: !request.isDone,
            };
            this.requests = [...this.requests];
          }
        },
        () => {
          this.msgService.error(
            this.translateService.instant('product.update_status_error'),
          );
          this.switchings[index] = false;
        },
      );
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
