import { HttpClient } from '@angular/common/http';
import { Location } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NzTreeNodeOptions } from 'ng-zorro-antd/tree';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { of, Subject, Observable } from 'rxjs';
import { mergeMap, takeUntil } from 'rxjs/operators';
import { NzMessageService } from 'ng-zorro-antd/message';
import { TranslateService } from '@ngx-translate/core';
import _ from 'lodash';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { NzDrawerService } from 'ng-zorro-antd/drawer';

import { environment } from '../../../../environments/environment';
import {
  CreateProductGQL,
  GetHierarchicalCategoriesGQL,
  GetProductDetailsQuery,
  ImageCreateWithoutProductInput,
  LanguageCode,
  PackagingtUnit,
  ProductStatus,
  Unit,
  UpdateProductGQL,
  SearchIngredientTranslationFragment,
} from '../../../../graphql/graphql';
import { FileService } from '../../../shared/services/file/file.service';
import { StringService } from '../../../shared/services/string/string.service';
import { LanguageService } from '../../../shared/services/language/language.service';
import {
  IngredientAnalysisComponent,
  IngredientAnalysisData,
} from '../../components/ingredient-analysis/ingredient-analysis.component';
import { UnitConverterService } from '../../../ingredient/services/unit-converter/unit-converter.service';

const IU = 'IU';

interface IngredientRow {
  id: number;
  name?: string;
  amount?: number;
  amountUnit?: Unit | typeof IU;
}

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss'],
})
export class ProductDetailsComponent implements OnInit, OnDestroy {
  ProductStatus = ProductStatus;
  units: Array<Unit | typeof IU> = [...Object.values(Unit), IU];
  packagingUnits: PackagingtUnit[] = Object.values(PackagingtUnit);
  destroy$: Subject<boolean>;
  product: GetProductDetailsQuery['product'];
  validateForm!: FormGroup;
  thumbnailFile?: NzUploadFile;
  thumbnailImage?: ImageCreateWithoutProductInput;
  loading = false;

  selectedIngredients: SearchIngredientTranslationFragment[] = [];
  ingredientAnalysisData?: IngredientAnalysisData;

  categories: Array<NzTreeNodeOptions> = [];
  languages: Map<LanguageCode, number>;
  formatterVnd = (value: number) => `₫ ${value}`;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private getHierarchicalCategoriesGQL: GetHierarchicalCategoriesGQL,
    private httpClient: HttpClient,
    private location: Location,
    private fileService: FileService,
    private createProductGQL: CreateProductGQL,
    private updateProductGQL: UpdateProductGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private stringService: StringService,
    private languageService: LanguageService,
    private breakpointObserver: BreakpointObserver,
    private drawerService: NzDrawerService,
    private unitConverter: UnitConverterService,
  ) {
    this.destroy$ = new Subject();
    this.validateForm = this.fb.group({
      name: [null, Validators.required],
      description: [null, Validators.required],
      slug: [null, Validators.required],
      price: [null, Validators.required],
      measure: [null, Validators.required],
      status: [true, Validators.required],
      categories: [[], Validators.required],
      brand: [null, Validators.required],
      ingredients: this.fb.array([]),
      barcodes: [[]],
      rdi: [null],
      packagingUnit: [null],
      netWeightPerPackage: [null],
      weightUnit: [null],
      netVolumePerPackage: [null],
      volumeUnit: [null],
      energyPerPackage: [null],
      energyUnit: [null],
    });
    this.languages = new Map();
    this.languages.set(LanguageCode.Vi, 1);
    this.languages.set(LanguageCode.En, 2);
  }

  ngOnInit(): void {
    this.route.data.pipe(takeUntil(this.destroy$)).subscribe(({ product }) => {
      this.product = product;
      this.thumbnailImage =
        (this.product?.thumbnail && {
          url: this.product.thumbnail.url,
          name: this.product.thumbnail.name,
          width: this.product.thumbnail.width || 0,
          height: this.product.thumbnail.height || 0,
        }) ||
        undefined;
      if (this.product?.videoTitle || this.product?.videoUrl) {
        this.addVideo();
      }
      if (
        // tslint:disable-next-line: deprecation
        this.product?.id &&
        this.product?.translations?.[0]?.imageDescription
      ) {
        this.addImageDescription();
      }
      this.validateForm.patchValue({
        name: this.product?.translations?.[0]?.name,
        description: this.product?.translations?.[0]?.description,
        imageDescription: this.product?.translations?.[0]?.imageDescription,
        slug: this.product?.translations?.[0]?.slug,
        price: this.product?.price || '',
        measure: this.product?.measure,
        status: this.product?.status === ProductStatus.Active,
        videoUrl: this.product?.videoUrl,
        videoTitle: this.product?.videoTitle,
        packagingUnit: this.product?.attributes?.packagingUnit,
        rdi: this.product?.attributes?.rdi,
        netWeightPerPackage: this.product?.attributes?.netWeightPerPackage,
        weightUnit: this.product?.attributes?.weightUnit,
        netVolumePerPackage: this.product?.attributes?.netVolumePerPackage,
        volumeUnit: this.product?.attributes?.volumeUnit,
        energyPerPackage: this.product?.attributes?.energyPerPackage,
        energyUnit: this.product?.attributes?.energyUnit,
        categories:
          this.product?.categories?.map(
            // tslint:disable-next-line: deprecation
            (category) => `${category.id}`,
          ) || [],
        brand: {
          name: this.product?.brand?.translations?.[0]?.name,
          brand: {
            // tslint:disable-next-line: deprecation
            id: this.product?.brand?.id,
          },
        },
        barcodes: this.product?.barcodes || [],
      });
      this.validateForm.setControl(
        'ingredients',
        this.fb.array(
          _.sortBy(
            this.product?.productToIngredients?.map((pti) => {
              return this.fb.group(
                this.unitConverter.convert(
                  {
                    id: pti.ingredient.id,
                    name: this.languageService.pick(
                      this.languages,
                      pti.ingredient.translations,
                    )?.name,
                    amount: pti.attributes?.amount || undefined,
                    amountUnit: pti.attributes?.amountUnit || undefined,
                  },
                  'source',
                ),
              );
            }) || [],
            'value.name',
          ),
        ),
      );
    });

    this.getHierarchicalCategoriesGQL
      .fetch()
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, error, errors }) => {
        if (error || errors) {
        }

        if (data) {
          this.categories = data.categories.map((category) => {
            return {
              // tslint:disable-next-line: deprecation
              key: `${category.id}`,
              title: category.translations?.[0]?.name,
              isLeaf: false,
              children: category.children?.map((child) => {
                return {
                  // tslint:disable-next-line: deprecation
                  key: `${child.id}`,
                  title: child.translations?.[0]?.name,
                  isLeaf: true,
                };
              }),
            };
          });
        }
      });
  }

  beforeUploadThumbnail = (file: NzUploadFile): boolean => {
    this.thumbnailFile = file;
    this.fileService.getBase64(
      file as unknown as File,
      (img: string, width: number, height: number) => {
        this.thumbnailImage = {
          url: img,
          name: this.thumbnailFile?.filename || '',
          width,
          height,
        };
      },
    );
    return false;
  };

  handleUploadImage = (
    blogInfo: any,
    success: (url: string) => void,
    failure: (error: any) => void,
  ): void => {
    const data = new FormData();
    data.append('file', blogInfo.blob());
    this.httpClient
      .post(`${environment.apiUri}/single-upload?useWatermark=false`, data)
      .subscribe(
        (response: any) => {
          success(response.url);
        },
        (error) => {
          failure(error);
        },
      );
  };

  addVideo(): void {
    this.validateForm.addControl(
      'videoTitle',
      this.fb.control(this.product?.videoTitle),
    );
    this.validateForm.addControl(
      'videoUrl',
      this.fb.control(this.product?.videoUrl),
    );
  }

  removeVideo(): void {
    this.validateForm.removeControl('videoTitle');
    this.validateForm.removeControl('videoUrl');
  }

  addImageDescription(): void {
    this.validateForm.addControl(
      'imageDescription',
      this.fb.control(this.product?.translations?.[0]?.imageDescription),
    );
  }

  removeImageDescription(): void {
    this.validateForm.removeControl('imageDescription');
  }

  isSmallDevice(): boolean {
    return this.breakpointObserver.isMatched([
      Breakpoints.XSmall,
      Breakpoints.Small,
    ]);
  }

  openIngredientAnalysis(): void {
    const drawerRef = this.drawerService.create<
      IngredientAnalysisComponent,
      undefined,
      IngredientAnalysisData
    >({
      nzTitle: this.translateService.instant('product.analyze_ingredient'),
      nzContentParams: {
        selectedIngredients: new Set<number>(
          this.ingredients.value.map((i: IngredientRow) => i.id),
        ),
        ...(this.ingredientAnalysisData || {}),
      },
      nzContent: IngredientAnalysisComponent,
      nzMaskClosable: false,
      nzWidth: this.breakpointObserver.isMatched([
        Breakpoints.XSmall,
        Breakpoints.Small,
      ])
        ? '100%'
        : '50%',
    });
    drawerRef.afterClose.subscribe((data) => {
      this.ingredientAnalysisData = data;
      if (!data?.newSelectedIngredients?.length) {
        return;
      }
      data.newSelectedIngredients?.forEach((ingredient) => {
        this.ingredients.push(
          this.fb.group({
            id: ingredient.id,
            name: ingredient.target,
            amount: null,
            amountUnit: null,
          }),
        );
      });
      this.ingredients.controls.sort((c1, c2) => {
        return (c1.value.name || '').localeCompare(c2.value.name);
      });
      this.ingredients.controls = [...this.ingredients.controls];
    });
  }

  addIngredients(): void {
    if (!this.selectedIngredients.length) {
      return;
    }
    this.selectedIngredients.forEach((it) => {
      this.ingredients.push(
        this.fb.group({
          id: it.ingredient.id,
          name: it.name,
          amount: null,
          amountUnit: null,
        }),
      );
    });
    this.ingredients.controls.sort((c1, c2) => {
      return (c1.value.name || '').localeCompare(c2.value.name);
    });
    this.ingredients.controls = [...this.ingredients.controls];
    this.selectedIngredients = [];
  }

  removeIngredient(index: number): void {
    this.ingredients.removeAt(index);
    this.ingredients.controls = [...this.ingredients.controls];
  }

  get ingredients(): FormArray {
    return this.validateForm.get('ingredients') as FormArray;
  }

  generateSlug(): void {
    const name = this.validateForm.get('name')?.value;
    if (!name?.trim().length) {
      return;
    }
    this.validateForm.patchValue({
      slug: this.stringService.stringToSlug(name),
    });
  }

  submitForm(): void {
    Object.values(this.validateForm.controls).forEach((control) => {
      control.markAsDirty();
      control.updateValueAndValidity();
    });

    if (this.validateForm.invalid) {
      return;
    }

    this.loading = true;

    let coverObservable: Observable<{
      id?: number;
      name?: string;
      url: string;
    }>;
    if (this.thumbnailFile) {
      const formData = new FormData();
      formData.append('file', this.thumbnailFile as unknown as File);
      coverObservable = this.httpClient.post<{ url: string }>(
        `${environment.apiUri}/single-upload?useWatermark=false`,
        formData,
      );
    } else {
      coverObservable = of({
        id: this.product?.thumbnail?.id,
        url: this.product?.thumbnail?.url || '',
      });
    }

    coverObservable
      .pipe(
        takeUntil(this.destroy$),
        mergeMap(({ id, url }) => {
          const ingredients: Array<IngredientRow> =
            this.validateForm.get('ingredients')?.value;
          const inputData = {
            status: this.validateForm.get('status')?.value
              ? ProductStatus.Active
              : ProductStatus.Inactive,
            price: this.validateForm.get('price')?.value,
            measure: this.validateForm.get('measure')?.value,
            videoTitle: this.validateForm.get('videoTitle')?.value,
            videoUrl: this.validateForm.get('videoUrl')?.value,
            brand: {
              connect: {
                id: this.validateForm.get('brand')?.value.brand.id,
              },
            },
            attribute: {
              create: {
                rdi: this.validateForm.get('rdi')?.value,
                packagingUnit: this.validateForm.get('packagingUnit')?.value,
                netWeightPerPackage: this.validateForm.get(
                  'netWeightPerPackage',
                )?.value,
                weightUnit: this.validateForm.get('weightUnit')?.value,
                netVolumePerPackage: this.validateForm.get(
                  'netVolumePerPackage',
                )?.value,
                volumeUnit: this.validateForm.get('volumeUnit')?.value,
                energyPerPackage:
                  this.validateForm.get('energyPerPackage')?.value,
                energyUnit: this.validateForm.get('energyUnit')?.value,
              },
            },
          };

          // tslint:disable-next-line: deprecation
          if (this.product?.id) {
            return this.updateProductGQL.mutate(
              {
                where: {
                  // tslint:disable-next-line: deprecation
                  id: this.product.id,
                },
                data: {
                  ...inputData,
                  categories: {
                    set: this.validateForm
                      .get('categories')
                      ?.value.map((value: string) => ({
                        id: +value,
                      })),
                  },
                  barcodes: {
                    create: this.validateForm
                      .get('barcodes')
                      ?.value?.filter((barcode: any) => barcode.label)
                      .map((barcode: any) => ({
                        value: barcode.label,
                      })),
                    set: this.validateForm
                      .get('barcodes')
                      ?.value?.filter((barcode: any) => barcode.id)
                      .map((barcode: any) => ({
                        id: barcode.id,
                      })),
                  },
                  thumbnail: {
                    connect:
                      (id && {
                        id,
                      }) ||
                      undefined,
                    create:
                      (!id &&
                        this.thumbnailImage && {
                          ...this.thumbnailImage,
                          url,
                        }) ||
                      undefined,
                  },
                  productToIngredients: {
                    create: ingredients.map((pti) => {
                      const targetPti = this.unitConverter.convert(
                        pti,
                        'target',
                      );
                      return {
                        ingredientId: targetPti.id,
                        attribute:
                          (pti.amount && {
                            create: {
                              amount: targetPti.amount,
                              amountUnit: targetPti.amountUnit as Unit,
                            },
                          }) ||
                          undefined,
                      };
                    }),
                  },
                  translations: {
                    create:
                      (!this.product?.translations?.[0] && [
                        {
                          name: this.validateForm.get('name')?.value,
                          description:
                            this.validateForm.get('description')?.value,
                          language: LanguageCode.Vi,
                          isOriginal: true,
                          imageDescription:
                            this.validateForm.get('imageDescription')?.value ||
                            null,
                          slug: this.validateForm.get('slug')?.value,
                        },
                      ]) ||
                      undefined,
                    set: this.product?.translations?.[0] && [
                      {
                        id: this.product.translations[0].id,
                        name: this.validateForm.get('name')?.value,
                        description:
                          this.validateForm.get('description')?.value,
                        language: LanguageCode.Vi,
                        isOriginal: true,
                        imageDescription:
                          this.validateForm.get('imageDescription')?.value ||
                          null,
                        slug: this.validateForm.get('slug')?.value,
                      },
                    ],
                  },
                },
              },
              {
                errorPolicy: 'all',
              },
            );
          } else {
            return this.createProductGQL.mutate(
              {
                data: {
                  ...inputData,
                  categories: {
                    connect: this.validateForm
                      .get('categories')
                      ?.value.map((value: string) => ({
                        id: +value,
                      })),
                  },
                  barcodes: {
                    create: this.validateForm
                      .get('barcodes')
                      ?.value.map((barcode: any) => ({
                        value: barcode.label,
                      })),
                  },
                  thumbnail: {
                    create: this.thumbnailImage && {
                      ...this.thumbnailImage,
                      url,
                    },
                  },
                  productToIngredients: {
                    create: ingredients.map((pti) => {
                      const targetPti = this.unitConverter.convert(
                        pti,
                        'target',
                      );
                      return {
                        ingredientId: targetPti.id,
                        attribute:
                          (pti.amount && {
                            create: {
                              amount: targetPti.amount,
                              amountUnit: targetPti.amountUnit as Unit,
                            },
                          }) ||
                          undefined,
                      };
                    }),
                  },
                  translations: {
                    create: [
                      {
                        name: this.validateForm.get('name')?.value,
                        description:
                          this.validateForm.get('description')?.value,
                        language: LanguageCode.Vi,
                        isOriginal: true,
                        imageDescription:
                          this.validateForm.get('imageDescription')?.value ||
                          null,
                        slug: this.validateForm.get('slug')?.value,
                      },
                    ],
                  },
                },
              },
              {
                errorPolicy: 'all',
              },
            );
          }
        }),
      )
      .subscribe(
        ({ errors, data }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant(
                `product.${this.product?.id ? 'update' : 'create'}_error`,
              ),
            );
          }

          if (data) {
            this.msgService.success(
              this.translateService.instant(
                `product.${this.product?.id ? 'update' : 'create'}_success`,
              ),
            );
            this.location.back();
          }
          this.loading = false;
        },
        () => {
          this.msgService.error(
            this.translateService.instant(
              `product.${this.product?.id ? 'update' : 'create'}_error`,
            ),
          );
          this.loading = false;
        },
      );
  }

  cancel(): void {
    this.location.back();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
