import { Component, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NzDrawerRef } from 'ng-zorro-antd/drawer';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { AnalyzeIngredientsGQL } from '../../../../graphql/graphql';

const CosDNARegExp = /(.+?)[\r\n]+.*?[\r\n]?\t(.*?)\t(.*?)\t(.*?)\t(.*)/g;
const SkincarismaRegExp = /(\d?)[\r\n]+\t(.*?)[\r\n]+\t(.*?)[\r\n]+/g;

enum IngredientSource {
  COSDNA = 1,
  SKINCARISMA = 2,
  OTHER = 3,
}

interface IngredientRow {
  source: string;
  id?: number;
  target?: string;
  ewg?: string;
  checked: boolean;
  disabled: boolean;
}

export interface IngredientAnalysisData {
  allChecked: boolean;
  searchText?: string;
  analyzedResult?: Array<IngredientRow>;
  newSelectedIngredients?: Array<IngredientRow>;
}

@Component({
  selector: 'app-ingredient-analysis',
  templateUrl: './ingredient-analysis.component.html',
  styleUrls: ['./ingredient-analysis.component.scss'],
})
export class IngredientAnalysisComponent implements OnInit, OnDestroy {
  IngredientSource = IngredientSource;
  destroy$: Subject<boolean>;

  searchText?: string;
  analyzing?: IngredientSource;
  analyzedResult?: Array<IngredientRow>;
  allChecked = true;
  selectedIngredients: Set<number> = new Set();

  constructor(
    private analyzeIngredientsGQL: AnalyzeIngredientsGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private drawerRef: NzDrawerRef<
      IngredientAnalysisComponent,
      IngredientAnalysisData
    >,
  ) {
    this.destroy$ = new Subject();
  }

  ngOnInit(): void {
    if (!this.analyzedResult || !this.selectedIngredients) {
      return;
    }
    this.analyzedResult = this.analyzedResult.map((ingredient) => {
      ingredient.checked = this.selectedIngredients.has(ingredient.id || 0);
      ingredient.disabled =
        this.selectedIngredients.has(ingredient.id || 0) || !ingredient.id;
      return ingredient;
    });
  }

  analyzeIngredients(source: IngredientSource): void {
    this.analyzing = source;
    if (!this.searchText) {
      this.analyzing = undefined;
      return;
    }

    let ingredients: string;
    const names: Array<string> = [];
    if (source !== IngredientSource.OTHER) {
      const regex =
        source === IngredientSource.COSDNA ? CosDNARegExp : SkincarismaRegExp;
      let array;
      while (true) {
        array = regex.exec(this.searchText);
        if (!array) {
          break;
        }
        names.push(array[source === IngredientSource.COSDNA ? 1 : 3]);
      }
      ingredients = names.join(';');
    } else {
      ingredients = this.searchText;
      names.push(...ingredients.split(';'));
    }
    this.analyzeIngredientsGQL
      .watch(
        {
          text: ingredients,
        },
        {
          notifyOnNetworkStatusChange: true,
          errorPolicy: 'all',
        },
      )
      .valueChanges.pipe(takeUntil(this.destroy$))
      .subscribe(({ data, loading, errors, error }) => {
        this.analyzing = (loading && source) || undefined;
        if (loading) {
          return;
        }

        if (error || errors) {
          this.msgService.error(
            this.translateService.instant('ui.message.error'),
          );
          return;
        }

        if (data) {
          this.analyzedResult = data.analyzeIngredients.ingredients.map(
            (ingredient, index) => {
              return {
                id: ingredient?.id,
                source: names[index],
                target: ingredient?.translations?.[0].name,
                ewg: ingredient?.ewg || undefined,
                checked: !!ingredient,
                disabled:
                  !ingredient || this.selectedIngredients.has(ingredient.id),
              };
            },
          );
        }
      });
  }

  checkAllIngredients(value: boolean): void {
    this.analyzedResult?.forEach((ingredient) => {
      if (ingredient.id) {
        ingredient.checked = value;
      }
    });
    this.refreshStatus();
  }

  refreshStatus(): void {
    const validData = this.analyzedResult?.filter((value) => !!value.id) || [];
    this.allChecked =
      validData.length > 0 &&
      validData.every((value) => value.checked === true);
  }

  pickAnalyzedIngredients(): void {
    if (!this.analyzedResult?.length) {
      return;
    }
    this.drawerRef.close(this.backupData(false));
  }

  cancel(): void {
    this.drawerRef.close(this.backupData(true));
  }

  private backupData(cancelled: boolean): IngredientAnalysisData {
    return {
      allChecked: this.allChecked,
      searchText: this.searchText,
      analyzedResult: this.analyzedResult,
      newSelectedIngredients: cancelled
        ? []
        : this.analyzedResult?.filter((i) => i.checked && !i.disabled),
    };
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
