import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzTableQueryParams } from 'ng-zorro-antd/table';
import { NzTreeNodeOptions } from 'ng-zorro-antd/tree';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  Brand,
  GetHierarchicalCategoriesGQL,
  GetProductListGQL,
  GetProductListQuery,
  GetProductListQueryVariables,
  OrderBy,
  ProductListFieldsFragment,
  ProductStatus,
  SearchBrandsGQL,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss'],
})
export class ProductListComponent implements OnInit, OnDestroy {
  ProductStatus = ProductStatus;
  destroy$: Subject<boolean> = new Subject();
  products: Array<ProductListFieldsFragment> = [];
  productsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetProductListQuery, GetProductListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  categories: Array<NzTreeNodeOptions> = [];
  searchForm: FormGroup;
  categoryId?: number;
  brandId?: number;
  text?: string;
  status?: ProductStatus;

  constructor(
    private getProductListGQL: GetProductListGQL,
    private getHierarchicalCategoriesGQL: GetHierarchicalCategoriesGQL,
    private searchBrandsGQL: SearchBrandsGQL,
    private route: ActivatedRoute,
    private router: Router,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private fb: FormBuilder,
  ) {
    this.searchForm = this.fb.group({
      product: [null],
      brand: [null],
      category: [null],
      status: [undefined],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.categoryId =
          parseInt(paramMap.get('category') || '0', 10) || undefined;
        this.brandId = parseInt(paramMap.get('brand') || '0', 10) || undefined;
        this.text = paramMap.get('text') || '';
        this.status = Object.values(ProductStatus).includes(
          paramMap.get('status') as ProductStatus,
        )
          ? (paramMap.get('status') as ProductStatus)
          : undefined;

        this.patchFormValue();

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getProductListGQL.watch(
            this.getVariables(),
            {
              notifyOnNetworkStatusChange: true,
              fetchPolicy: 'network-only',
            },
          );
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(({ data, loading, error, errors }) => {
              this.loading = loading;
              if (loading) {
                return;
              }

              if (error || errors) {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
                return;
              }

              if (data) {
                this.products = data.products;
                this.productsCount = data.productsConnection.aggregate.count;
              }
            });
        }
      });

    this.getHierarchicalCategoriesGQL
      .fetch()
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data, error, errors }) => {
        if (error || errors) {
        }

        if (data) {
          this.categories = data.categories.map((category) => {
            return {
              // tslint:disable-next-line: deprecation
              key: `${category.id}`,
              title: category.translations?.[0]?.name,
              isLeaf: false,
              children: category.children?.map((child) => {
                return {
                  // tslint:disable-next-line: deprecation
                  key: `${child.id}`,
                  title: child.translations?.[0]?.name,
                  isLeaf: true,
                };
              }),
            };
          });
        }
      });
  }

  patchFormValue(): void {
    if (this.categoryId && this.searchForm.controls.category.untouched) {
      this.searchForm.patchValue({
        category: `${this.categoryId}`,
      });
    }

    if (
      this.brandId &&
      this.searchForm.controls.brand.untouched &&
      this.searchForm.controls.brand.pristine
    ) {
      this.searchBrandsGQL
        .fetch({
          where: {
            id: this.brandId,
          },
          first: 1,
        })
        .pipe(takeUntil(this.destroy$))
        .subscribe(({ data }) => {
          if (data?.brands?.length) {
            this.searchForm.patchValue({
              brand: {
                name: data.brands[0].translations?.[0]?.name,
                brand: {
                  // tslint:disable-next-line: deprecation
                  id: data.brands[0].id,
                } as Brand,
              },
            });
          }
        });
    }

    if (this.text && this.searchForm.controls.product.untouched) {
      this.searchForm.patchValue({
        product: this.text,
      });
    }

    if (this.searchForm.controls.status.untouched) {
      this.searchForm.patchValue({
        status: this.status || undefined,
      });
    }
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetProductListQueryVariables {
    return {
      where: {
        nameContains: this.text || undefined,
        categoriesSome: {
          id: this.categoryId || undefined,
        },
        brand: {
          id: this.brandId || undefined,
        },
        status: this.status || undefined,
      },
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: [
        {
          id: OrderBy.Desc,
        },
      ],
    };
  }

  handleQueryParamsChange(params: NzTableQueryParams): void {
    this.router.navigate([], {
      queryParams: {
        page: params.pageIndex,
        size: params.pageSize,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    const searchData = this.searchForm.value;
    const brandId = searchData.brand?.brand.id || undefined;
    const categoryId = parseInt(searchData.category, 10) || undefined;
    const text = searchData.product || undefined;
    const status = searchData.status || undefined;

    this.router.navigate([], {
      queryParams: {
        text,
        brand: brandId,
        category: categoryId,
        status,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  trackById(index: number, item: any): number {
    return item.id;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
